

.group__header {
  margin: 0;
}
@media (min-width: @desktop-width) {
  .group__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 69px;
  }
}


.group__items {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 1px solid #bb3fb1;
}

@media (min-width: @desktop-width) {
  .group__items {
    width: 568px;
  }
}

.group__items-fl {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 1px solid #bb3fb1;
}

@media (min-width: @desktop-width) {
  .group__items-fl {
    width: 100%;
    margin-bottom: 69px;
  }
}
.group__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  border: none;
}



.group__comment {
  margin: 0;
  padding: 0 20px;
  box-sizing: border-box;
}



